
.container {
    max-width: 600px;
    margin: 40px auto; /* Auto margin for centering vertically and horizontally */
    padding: 20px;
    background-color: rgb(222, 5, 5);
    border-radius: 10px;
  }
  
  
  .input-file {
    margin-bottom: 20px;
  }
  
  .playlist {
    list-style-type: none;
    padding: 0;
  }
  
  .playlist li {
    margin-bottom: 10px;
  }
  
  .playlist li button {
    background-color: #007bff;
    color: #fff;
    padding: 8px 12px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .playlist li button:hover {
    background-color: #0056b3;
  }
  
  .now-playing {
    margin-top: 20px;
  }
  
  .now-playing audio {
    width: 100%;
  }
  